
import { defineComponent, ref } from 'vue';
import VueCountdown from '@chenfengyuan/vue-countdown';

export default defineComponent({
  name: 'HomeView',
  setup() {
    const color = ref('black');
    return {
      color, 
      timeData: (data: any) => {
        if (data.totalMinutes <= 5) {
          color.value = 'red';
        } else if (data.totalMinutes <= 30) {
          color.value = 'yellow';
        }
      }
    }
  },
  components: {
    VueCountdown
  },
});
